export default {
	data() {
		return {
			secondArr:[{
				id: 1,
				name: '25秒'
			},{
				id: 2,
				name: '不读秒'
			},],
			secondIndex: 0,
			typeArr:[{
				id: 1,
				name: '数图配对',
				path:'/pages/train/imageMemory/numImgPair'
			},{
				id: 2,
				name: '数图单选',
				path:'/pages/train/imageMemory/numImgSelect'
			},{
				id: 3,
				name: '数图计算',
				path:'/pages/train/imageMemory/numImgCount'
			},{
				id: 4,
				name: '数图排序',
				path:'/pages/train/imageMemory/numImgSort'
			},],
			typeIndex: 0,
		};
	},
	methods:{
		bindSecondChange(e){
			this.secondIndex = e.detail.value
		},
		bindTypeChange(e){
			this.typeIndex = e.detail.value
		},
		goBack(){
			uni.navigateBack({
				delta:1
			})
		},
		gotoUrl(){
			uni.navigateTo({
				url: this.typeArr[this.typeIndex].path
			})
		}
	}
}
